import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'

import AuthModal from './components/auth_modal'
import Feed from './components/feed'

const URL = process.env.NODE_ENV == 'development' ? 'http://localhost:5000' : 'https://wikibits.herokuapp.com'

const Home = () => (
  <div>
    <div className="jumbotron jumbotron-fluid bg-white" style={{padding: '1em 1em 1em 1em'}}>
      <h2 className="display-5">Organizing the world's information into small little bits</h2>
      {/* <p className="lead">Join the community of people working on gathering and summarizing articles into bits of facts.</p> */}
      {/* <p className="lead">
        <a className="btn btn-primary btn-lg" href="#" role="button">Sign Up</a>
      </p> */}
    </div>
    <Feed 
      URL={URL}
    />
  </div>
)

const Topic = ({ match }) => (
  <div>
    <h3>{match.params.topicId}</h3>
  </div>
)

const Composer = ({ match }) => (
  <div className='bg-white' style={{padding: '1em'}}>
    <h2>Compose</h2>
    <div className="card">
      <div className="card-body">
        <form>
          <div className="form-group">
            <label for="exampleFormControlTextarea1">Write a Bit</label>
            <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
          </div>
          <div className="form-group">
            <label for="exampleFormControlTextarea1">Article Section Reference</label>
            <input type="text" className="form-control" id="exampleFormControlInput1" placeholder="Add section link" />
          </div>
          <a href="#" className="btn btn-primary">Publish</a>
        </form>
      </div>
    </div>
    <ul>
      <li>
        <Link to={`${match.url}/rendering`}>
          Rendering with React
        </Link>
      </li>
      <li>
        <Link to={`${match.url}/components`}>
          Components
        </Link>
      </li>
      <li>
        <Link to={`${match.url}/props-v-state`}>
          Props v. State
        </Link>
      </li>
    </ul>

    <Route path={`${match.url}/:topicId`} component={Topic}/>
    <Route exact path={match.url} render={() => (
      <h3>Please select a topic.</h3>
    )}/>
  </div>
)

export default class App extends Component {
    state = {
      user: null
    }

    render() {
      return (
        <Router>
          <div className='container-fluid bg-light'>
            <nav className="navbar navbar-expand-lg navbar-light bg-white" style={{borderBottom: '1px solid grey'}}>
              <Link className="navbar-brand" to="/">WikiBits 🐾</Link>
              <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                {/* <form className="form-inline my-2 my-lg-0">
                  <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
                </form> */}
                <ul className="navbar-nav ml-auto">
                  <li className="nav-item">
                    <a className="btn btn-link" href="https://twitter.com/wikibitsapp" target="_blank">🐦 @WikiBitsApp</a>
                  </li>
                  {/* <li className="nav-item">
                    <Link className="nav-link" to="/compose">Compose</Link>
                  </li> */}
                  {/* <li className="nav-item active">
                    <button type="button" className="btn btn-link" data-toggle="modal" data-target="#login-modal">Sign Up</button>
                  </li>
                  */}
                  {/* <li className="nav-item">
                    <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#login-modal">Login</button>
                  </li> */}
                </ul>
                <AuthModal
                  URL={URL}
                  setAuthUser={(user) => { this.setState({ user }) }}
                />
              </div>
            </nav>
            <Route exact path="/" component={Home}/>
            <Route path="/compose" component={Composer}/>
          </div>
        </Router>
    )
  }
}


