import React, { Component } from 'react';

export default class FeedItem extends Component {
  render() {
    let { content, topic, reference_url } = this.props.data

    return (
      <div className="card" style={{marginBottom: '0.25em'}}>
        <div className="card-body">
          <a href={reference_url}>{topic}</a>
          <p className="card-text">{content}</p>
        </div>
      </div>
    )
  }
}


