import React, { Component } from 'react'
import $ from 'jquery'

export default class AuthModal extends Component {
  state = {
    username: '',
    password: '',
    loginStatus: {
      ok: true,
      message: ''
    }
  }

  componentWillUpdate(nextProps, nextState) {
    console.log('nextState', nextState)
  }

  clickLogin = (event) => {
    event.preventDefault()
    let { URL, setAuthUser } = this.props
    console.log('this.usernameInput', this.usernameInput.value)
    fetch(`${URL}/login`, {
      method: 'POST',
      body: JSON.stringify({
        username: this.usernameInput.value,
        password: this.passwordInput.value
      }), 
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
    .then((response) => response.json())
    .then((response) => {
      if(!response.ok) {
        throw Error(response.message)
        return response
      }
      setAuthUser(response)
      $('#login-modal').modal('hide')
    })
    .catch((error) => {
      console.log('error', error.message)
      this.setState({loginStatus: {...error}})
    })
  }

  render() {
    return (
      <div className="modal fade" id="login-modal" tabindex="-1" role="dialog" aria-labelledby="login-modal-label" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Modal title</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <form>
                {!this.state.loginStatus.ok ?
                  <div style={{color: 'red', fontSize: 12}}>
                    Something went wrong. Either your username or password was incorrect.
                  </div>
                  :
                  null
                }
                <div className="form-group">
                  <label for="username-input">Username</label>
                  <input type="text" className="form-control" id="input-username" placeholder="Username"
                    ref={(input) => { this.usernameInput = input }}
                  />
                </div>
                <div className="form-group">
                  <label for="input-password">Password</label>
                  <input type="password" className="form-control" id="input-password" placeholder="Password"
                    ref={(input) => { this.passwordInput = input }}
                  />
                </div>
                <button type="submit" className="btn btn-primary"
                  onClick={this.clickLogin}
                >
                  Submit
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    )
  }
}


