import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'
import FeedItem from './feed_item'

class Feed extends Component {
  state = {
    items: []
  }

  componentDidMount() {
    let { URL } = this.props
    
    fetch(`${URL}/tidbits`)
      .then((data) => data.json())
      .then((items) => {
        this.setState({items})
      })
  }

  render() {
    let { items } = this.state
    
    return (
      <div style={{margin: '2em 0em 0em 0em'}}>
        {items.length ? 
          <div>
            {items.map((item) => (
                <FeedItem
                  key={item.id}
                  data={item}
                />
              )
            )}
            <input 
              className="btn btn-primary btn-lg btn-block" 
              type="button" 
              value="Click here to see more Bits!" 
              onClick={() => window.location.reload()}
            />
          </div>
          :
          <div className="jumbotron jumbotron-fluid">
            <div className="container">
              <h1 className="display-4">Loading 🤖!</h1>
              <p className="lead">
                <p>This project is hosted on a free service, so sometimes the server goes to sleep 😴.
                  Support us and keep the servers awake by telling your friends about WikiBits 🐾.
                </p>
                <p>In the meantime here's a WikiBit while you wait ...</p>
                <h5>Wrap Rage</h5>
                <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/10/Wrap_Rage_Example.jpg/1200px-Wrap_Rage_Example.jpg" alt="wrap rage" width={220} />
                <p>It's when a person is frustrated from their inability to open super packaging that is generally heat sealed by plastic blister packs. People in wrap rage can often injure themselves in the process of aggressively attempting to open the packaging. </p>
              </p>
            </div>
          </div>
        }
      </div>
    )
  }

  }

export default Feed


